import React, { useState ,useEffect} from 'react'
import axios from 'axios'
import {message} from 'antd'
import { Link,useNavigate } from 'react-router-dom'
import '../styles/LoginPage.css'

const Login = () => {
  const navigate = useNavigate()
  const loginData = {
    email:"",password:""
  }
  const [logval,setLogval] = useState(loginData)

  const loginvalHandler = (e) =>{
    setLogval({...logval,[e.target.name]:e.target.value})
  }


  //login
  const loginSubmithandler = async (e) =>{
    e.preventDefault();
    try {
     const {data} = await axios.post('/api/v1/users/login',logval)
      message.success('login successfully')
      localStorage.setItem('user', JSON.stringify({...data.user,password:''}))
      navigate('/')
    } catch (error) {
      console.log(error)
    }
  }

  //prevent for login user
  useEffect(()=>{
    if(localStorage.getItem('user')){
      navigate('/')
    }
  },[navigate]);

  return (
    <>
      <div className="login-page">
        <form className=" login-form col-6 m-auto" onSubmit={loginSubmithandler} >
          <h1 className="text-center mb-5">
            LogIn Form
          </h1>
          <div className="mb-3">
            <label  >
              Email
            </label>
            <input
              type="email"
              name="email"
              value={logval.email}
              onChange={loginvalHandler}
              className="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
            />
          </div>
          <div className="mb-3">
            <label  >
              Password
            </label>
            <input
              type="password"
              name="password"
              value={logval.password}
              onChange={loginvalHandler}
              className="form-control"
              id="exampleInputPassword1"
            />
          </div>
          <div className="d-flex ">
            <Link to='/register' >User Not found ? Click Here to register</Link>
            <button type="submit" className="btn btn-primary" >
              LogIn
            </button>
          </div>
        </form>
      </div>
      
    </>
  )
}

export default Login