
import React, { useState, useEffect } from 'react'
import { Form, Input, message, Modal, Select, Table } from 'antd';//now i'll use  ant design for saving ⌚ in useState hook and i will use for table
import {EditOutlined,DeleteOutlined} from '@ant-design/icons' // icons update and delete
import Layout from '../components/Layout/Layout'
import axios from 'axios'


const HomePage = () => {
  const [showModal, setShowModal] = useState(false);
  const [allTransections, setAllTransections] = useState([])
  const [editData,setEditData] = useState(null)


  //table data
  const columns = [
    {
      title: 'Date',
      dataIndex: 'date',
    },
    {
      title: 'Amount',
      dataIndex: 'amount',
    },
    {
      title: 'Type',
      dataIndex: 'type',
    },
    {
      title: 'Category',
      dataIndex: 'category',
    },
    {
      title: 'Refrence',
      dataIndex: 'refrence',
    },
    {
      title: 'Action',
      render:(text,record)=>(
        <div>
          <EditOutlined onClick={()=>{
            setEditData(record)
            setShowModal(true)
          }} />
          <DeleteOutlined className='mx-2' onClick={()=>{handleDelete(record)}}/>
        </div>
      )
    },

  ]


      //get all transection from database and show 
      const getAllTransection = async () => {
        try {
          const user = JSON.parse(localStorage.getItem('user'))
          const res = await axios.post('/api/v1/transections/get-transection', { userid: user._id })
          setAllTransections(res.data)
          console.log(res.data)
        } catch (error) {
          console.log(error)
          message.error('something is wrong when fteching')
        }
      }


  //useEffect for fteching data from database
  useEffect(() => {
    getAllTransection();
  }, []) //type filter when change type filter

  //delete data 
  const handleDelete = async (record) =>{
    try {
      await axios.post("/api/v1/transections/delete-transection",{transectioId:record._id})
      message.success('transection deleted ')
    } catch (error) {
      console.log(error)
      message.error('unable to delete')
    }
  }


  //push transection value in database with user id in localstorage in 
  const handelSubmit = async (values) => {
    try {
      const user = JSON.parse(localStorage.getItem('user'));  //update data 
      if(editData){

        await axios.post("/api/v1/transections/edit-transection", { 
          payload:{
            ...values,
            userId: user._id
          },
          transectioId: editData._id
        })
      message.success('transection edit successfully')

      }else{
        await axios.post("/api/v1/transections/add-transection", { ...values, userid: user._id })
      message.success('transection added successfully')
      }
      setShowModal(false);
      setEditData(null)
    } catch (error) {
      message.error('faild to add transection')
    }
  }


  return (
    <Layout>
      <div className="filters">
        <div>
          <h5 style={{fontWeight:'bolder'}}>Data List</h5>
        </div>
        <div><button className='btn btn-primary' onClick={() => { setShowModal(true) }}>Add New</button></div>
      </div>
      <div className="content">
        <Table columns={columns} dataSource={allTransections} />
      </div>

      <Modal title={editData ? 'Edit transection' : 'Add transection'} // add title when you click on Add New and  edit icon 
        open={showModal}
        onCancel={() => { setShowModal(false) }}
        footer={false}
      >
        <Form layout='vertical' onFinish={handelSubmit} initialValues={editData}>
          <Form.Item label="Amount" name='amount'>
            <Input type='text' />
          </Form.Item>
          <Form.Item label="Type" name='type'>
            <Select>
              <Select.Option value="income">Income</Select.Option>
              <Select.Option value="expense">Expense</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="Category" name='category'>
            <Select>
              <Select.Option value="Salary">Salary</Select.Option>
              <Select.Option value="tip">Tip</Select.Option>
              <Select.Option value="food">Food</Select.Option>
              <Select.Option value="movie">Movie</Select.Option>
              <Select.Option value="bills">Bills</Select.Option>
              <Select.Option value="medical">Medical</Select.Option>
              <Select.Option value="fee">Fee</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="Refrence" name="refrence">
            <Input type='text' />
          </Form.Item>
          <Form.Item label="Description" name="description">
            <Input type='text' />
          </Form.Item>
          <Form.Item label="Time" name="date">
            <Input type='date' />
          </Form.Item>
          <div className="d-flex justify-content-end">
            <button type='submit' className='btn btn-primary'>
              {" "}
              SAVE
            </button>
          </div>
        </Form>
      </Modal>
    </Layout>
  )
}
export default HomePage